import React from 'react'
import ItemBarButton from './itemBarButton'
import ItemBarContent from './itemBarContent'

class User extends React.Component{
  constructor(props){
    super(props)
    this.wechatLogin = this.wechatLogin.bind(this)
    this.nameClick = this.nameClick.bind(this)
  }

  wechatLogin(){

  }

  nameClick(){

  }

  render(){
    return <div
      style = {{
        backgroundColor: '#eaeaea',
        width: '100%',
        height: '100%',
        paddingTop: '8px'
      }}
    >
      <ItemBarButton title = "" onClick = {this.wechatLogin}/>

      <ItemBarContent label = "" content = "" onClick = {this.nameClick}/>
    </div>
  }
}

export default User



