import React from 'react'
import Desktop from './desktop'

const testContentList = [
  {
    icon: 'img/game.png',
    name: '',
    navLink: 'train/static',
    para: {}
  },
]

class TrainDesktop extends React.Component{
  render(){
    return <Desktop
          contentList = {testContentList}
          column = {3}
        />
  } 
}

export default TrainDesktop



